const galleryListEl = document.querySelector('.gallery');
galleryListEl.style.padding = '10px';
galleryListEl.style.display = 'flex';
galleryListEl.style.justifyContent = 'center';
galleryListEl.style.flexWrap = 'wrap';
galleryListEl.style.rowGap = '24px';
galleryListEl.style.gap = '24px';

const imagesListHTML = images.map(({preview, original, description}) => `<li class="gallery-item" style="list-style: none;">
  <a class="gallery-link" href="${original}">
    <img
      class="gallery-image"
      src="${preview}"
      alt="${description}"
      style="height: 200px; width: 360px;"
    />
  </a>
</li>
`
).join('');

galleryListEl.innerHTML = imagesListHTML;

new SimpleLightbox('.gallery a', {
    captions: true,
    captionsData: 'alt',
    captionDelay: 250
});